// Base

*,
*:after,
*:before {
    box-sizing: border-box;
    position: relative;
    padding: 0;
    margin: 0;
    z-index: 1;
}

html {
    position: relative;
    height: 100%;
    z-index: 1;
}

body {
    position: relative;
    z-index: 1;
}




.navigation {
    width:100%;
    height: 35px;
    background-color: lighten(black, 15%);
    position: absolute;
    display: block;
    margin-bottom: 20px;
    border-bottom: 1px solid darken(#EAEAEA, 10%);
    position: absolute;
    z-index: 9999;

    table {
        @include vertical-align;
        color: #B3B3B3;
        font-family: 'Oxygen', sans-serif;
        float: right;
        margin-right: 20px;

        td {
            padding: 0px 20px 0px 20px;
        }

        td:hover {
            color: darken(#B3B3B3, 30%);
        }


    }
}

.header {
    width: 100%;
    height: 450px;
    margin-bottom: 200px;

    img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        padding-top: 5%;
        height: 500px;
    }

}

.main {
    @include clearfix;
    .content {
        width: 75%;
        float: left;
        height: 350px;
        background-color: darken(white, 5%);
        margin: 20px auto 20px auto;
        display: block;
    }

    .title {
        @include clearfix;
        width: 20%;
        display: block;
        margin: 30px 0px 10px 50px;
        float: left;

        h1 {
            font-size: 1000%;
            font-family: 'Oxygen', sans-serif;
        }
    }
}

.about {

}
.work {

    .gallery {
        @include vertical-align;

        width: 100%;
        float: right;
        padding: 0px 20px 0px 20px;
        empty-cells: show;
        border-collapse: separate;
        border-spacing: 100px;

        table{
            border-collapse: separate;
            border-spacing: 15px;

            td {
            // padding: 0px 20px 0px 20px;
            border: 3px solid darken(white, 10%);
            width: 300px;
            height: 300px;
            background-color: white;
            }

            #special:hover {
                width: 400px;

            }

            td:hover {
                background-color: lighten(blue, 30%);
            }
        }




    }



}
.contact {
    margin-bottom: 5%;

}

.footer {
    width: 100%;
    height: 500px;

    img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        padding-top: 5%;
        height: 500px;
    }
}